main {
  display: flex;
  flex-flow: row nowrap;
}
.video__container {
  width: 320px;
  height: 240px;
  position: relative;
  margin-bottom: 4rem;
  margin-right: 2rem;
}

#video {
  border: 1px solid black;
  box-shadow: 2px 2px 3px black;
  width: 100%;
  height: 100%;
}

#startbutton {
  display: block;
  position: absolute;
  outline: none;
  left: 50%;
  top: 85%;
  transform: translate(-50%);
  background-color: rgba(0, 150, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.7);
  box-shadow: 0px 0px 1px 2px rgba(0, 0, 0, 0.2);
  font-size: 14px;
  font-family: 'Lucida Grande', 'Arial', sans-serif;
  color: rgba(255, 255, 255, 1);
  cursor: pointer;
}

#photo {
  border: 1px solid black;
  box-shadow: 2px 2px 3px black;
  width: 250px;
  height: 240px;
}

#canvas {
  display: none;
}

.camera {
  width: 340px;
  display: inline-block;
}

.output {
  width: 340px;
  display: inline-block;
}

.contentarea {
  font-size: 16px;
  font-family: 'Lucida Grande', 'Arial', sans-serif;
  width: 760px;
}
